<template>
  <div class="map-view">
    <div id="main" ref="main">

    </div>
  </div>
</template>

<script>
import geoJson from '@/assets/data.json'
export default {
  data() {
    return {
      
    }
  },
  mounted() {
    let myChart = this.$echarts.init(this.$refs['main'])
    this.$echarts.registerMap('china', geoJson)   //注册可用的地图，必须包含geo组件或者map图表类型的时候才可以使用
    let option = {
      backgroundColor: 'rgba(121,145,209,0.5)',
      
      geo: {
        map: 'china',
        aspectScale: 0.75,  //地图缩放长宽比
        zoom: 1.1,
        itemStyle: {
          normal: {
            // borderWidth:1,
            areaColor: {
              type: 'radial',
              x: 0.5,
              y: 0.5,
              r: 0.8,
              colorStops: [{
                offset: 0,
                color: '#e2e1e4'    //0%处颜色
              }, 
              {
                offset: 1,
                color: '#8076a3'    //100%处颜色
              }],
              globalCoord: true
            },
            
            shadowColor: "#092f8f", // 阴影-外发光
            shadowBlur: 5,  //模糊级数
            shadowOffsetX: 5,  //偏移量
            shadowOffsetY: 6,
          },
        },
        regions: [{
          name: '南海诸岛',
          itemStyle: {
            opacity: 0.5
          }
        }] 
      },

      series: [{
        //配置地图相关参数,绘制地图,这个对象是关于地图图表的相关设置
        type: 'map',
        label: {
          normal: {
            show: true,  //常态下是否显示省份名称
            textStyle: {
              color: '#000000'
            }
          },
          emphasis: {
            textStyle: {
              color: '#FFFFFF',
              fontSize: 15
            }
          }
        },
        zoom: 1.1,
        map: 'china',
        itemStyle: {
          normal: {
            // borderWidth:1,
            areaColor: "#8076a3",
            areaColor: {
              type: 'radial',
              x: 0.5,
              y: 0.5,
              r: 0.8,
              colorStops: [{
                offset: 0,
                color: '#e2e1e4'    //0%处颜色
              }, 
              {
                offset: 1,
                color: '#8076a3'    //100%处颜色
              }],
              globalCoord: true
            }
          },
          emphasis: {
            borderWidth: 2,
            borderColor: "#fff",
            areaColor: "#ed5a65", //悬浮区背景
          }
        },
      }]
    }
    myChart.setOption(option)
    
  }
}
</script>

<style lang="scss" scoped>
.map-view {
  width: 100%;

  #main{
    width: 100%;
    height: 500px;
    // background-color: rgba(127, 255, 212, 0.203);
  }
}
</style>